'use strict';

import { addToLocalStorage } from '../localStorage/localStorage';
import { MODAL_TEST } from '../testfiles/modal_test';
import { closeModal } from './closeModal';
import { markupData } from './modalMarkup';

console.table(MODAL_TEST.backdrop_path);
const mainContent = document.querySelector('#main');

// funkcja otwierajca modal
const openModal = e => {
  // przerywa funkcj, jeli zdarzenie nie wystpuje na elemencie z klas "movie-card".
  if (!e.target.closest('.movie-card')) {
    return;
  }

  // utworzenie nowego elementu backdrop i dodanie klasy "backdrop"
  const backdrop = document.createElement('div');
  backdrop.classList.add('backdrop');
  backdrop.innerHTML = markupData(MODAL_TEST);

  // dodanie elementu backdrop do ciaa dokumentu
  document.body.appendChild(backdrop);

  // blokuje moliwo skrolowania w momencie uruchomienia modala
  document.body.classList.add('overflow-off');

  closeModalButton = document.querySelector('#modal__close');

  // pobranie id filmu
  const movieId = MODAL_TEST.id;
  const watchedButton = document.getElementById('watched');
  const queueButton = document.getElementById('queue');
  /*
    Ten fragment kodu reaguje na kliknicie przyciskw "watchedButton" i "queueButton" i wywouje 
    funkcj "addToLocalStorage" z odpowiednimi argumentami. Funkcja ta zapisuje identyfikator filmu 
    do lokalnego magazynu przegldarki pod kluczem "user" i list "watched" lub "queue", w zalenoci od przycisku, 
    ktry zosta kliknity.
  */
  watchedButton.addEventListener('click', () =>
    addToLocalStorage(movieId, 'watched')
  );
  queueButton.addEventListener('click', () =>
    addToLocalStorage(movieId, 'queue')
  );
  // dodanie nasuchiwania na zdarzenie 'click' do elementu `closeModalButton` z funkcj `closeModal`
  closeModalButton.addEventListener('click', closeModal);
};

// dodanie nasuchiwania na zdarzenie 'click' do elementu `mainContent` z funkcj `openModal`
mainContent.addEventListener('click', openModal);

export { openModal };
